export function createMarkup(images) {
    return images
      .map(
        ({
          webformatURL,
          largeImageURL,
          tags,
          likes,
          views,
          comments,
          downloads,
        }) =>
          `<div class="photo-card">
            <a href="${largeImageURL}">
              <img src="${webformatURL}" alt="${tags}" width="640" height="426" loading="lazy" />
            </a>
            <div class="info">
              <p class="info-item">
                <b>Likes:</b>
                ${likes}
              </p>
              <p class="info-item">
                <b>Views:</b>
                ${views}
              </p>
              <p class="info-item">
                <b>Comments:</b>
                ${comments}
              </p>
              <p class="info-item">
                <b>Downloads:</b>
                ${downloads}
              </p>
            </div>
          </div>`
      )
      .join('');
  }